import React from 'react';
import './banner.css';
import tplogo from '../../assets/mylogo.png';

export default function Banner() {


    return (
        <div className="banner-wrapper">
            <div className="left-slope"></div>
            <div className="banner-container">
                <div className="banner-content">
                    <img src={tplogo} alt="Tobiasz Pokorniecki logo" />
                    <div className="banner-contact-container">
                        <a href="mailto:tobiasz.pokorniecki@o2.pl" className="banner-contact-item">
                            <i className="material-icons md-white">mail</i>
                            <span>tobiasz.pokorniecki@o2.pl</span>
                        </a>
                        {/* <a href="tel:730415500" className="banner-contact-item">
                            <i className="material-icons md-white">phone</i>
                            <span>730 415 500</span>
                        </a> */}
                    </div>
                </div>
            </div>
            <div className="right-slope"></div>
        </div>
    )


}