import React, { useState, useEffect, useRef } from 'react';
import './projectsslider.css';
import { SwitchTransition, CSSTransition } from "react-transition-group";
import ghprojectlogo from '../../assets/ghprojectlogo.png';
import Lightbox from 'react-image-lightbox';
import { isMobile } from 'react-device-detect';
import { useSwipeable } from 'react-swipeable';

// PROJECT PHOTOS
import bell1 from '../../assets/project_shots/bell1.png';
import bell2 from '../../assets/project_shots/bell2.png';
import bell3 from '../../assets/project_shots/bell3.png';
import bell4 from '../../assets/project_shots/bell4.png';

import coin1 from '../../assets/project_shots/coin1.png';
import coin2 from '../../assets/project_shots/coin2.png';
import coin3 from '../../assets/project_shots/coin3.png';
import coin4 from '../../assets/project_shots/coin4.png';

import gwent1 from '../../assets/project_shots/gwent1.png';
import gwent2 from '../../assets/project_shots/gwent2.png';

import ico1 from '../../assets/project_shots/ico1.png';
import ico2 from '../../assets/project_shots/ico2.png';
import ico3 from '../../assets/project_shots/ico3.png';
import ico4 from '../../assets/project_shots/ico4.png';
// -----------------


export default function ProjectsSlider() {

    let [pickedProject, setPickedProject] = useState(0);
    let projectChosenByUser = useRef(false);
    let [currentPhoto, setCurrentPhoto] = useState(0);
    let [isLightboxOpened, setIsLightboxOpened] = useState(false);
    let [photoIndex, setPhotoIndex] = useState(0);
    let ticks = useRef(0);
    let pauseInterval = useRef(false);
    let [slideDirection, setSlideDirection] = useState('toleft');
    let transitionNode = useRef(null);
    // SWIPEABLE
    let swipeHandlers = useSwipeable({
        onSwipedLeft: () => {
            setSlideDirection('toleft');
            if (pickedProject + 1 >= projects.length) {
                setPickedProject(0);
            } else {
                setPickedProject(pickedProject + 1);
            }
            ticks.current = 0;
            setCurrentPhoto(0);
        },
        onSwipedRight: () => {
            setSlideDirection('toright');
            if (pickedProject - 1 < 0) {
                setPickedProject(projects.length - 1);
            } else {
                setPickedProject(pickedProject - 1);
            }
            ticks.current = 0;
            setCurrentPhoto(0);
        }
    })
    // -----



    let projects = [
        {
            name: 'JudiBell App',
            technologies: ['React.js', 'SCSS', 'JWT', 'REST'],
            description: 'Zaliczeniowy projekt zespołowy, w którym odpowiedzialny byłem za front-end. Celem projektu było utworzenie responsywnej aplikacji webowej „JudiBell” umożliwiającej rezerwację stolików w restauracjach oraz lokalach. Wyróżnikiem aplikacji jest interaktywny sposób wyboru miejsca w lokalu umożliwiający dokonanie rezerwacji najmniejszą możliwą ilością kliknięć bez konieczności przeładowywania strony. Aplikacja umożliwia również kontrolę nad zarezerwowanymi stolikami obsłudze, a także zarządzanie wszelkimi aspektami lokalu przez menadżerów. Budowa strony pozwala na szybki rozwój oraz prostą rozbudowę o nowe komponenty.\nWspółautorzy: Maciej Łukasik, Maciej Ertmański',
            progress: { proc: 100, comm: 'Ukończony' },
            link: '',
            photos: [bell1, bell2, bell3, bell4]
        },
        {
            name: 'JudiCoin App',
            technologies: ['Flutter', 'Firebase', 'Firestore'],
            description: 'Mobilna aplikacja umożliwiająca tworzenie budżetów kontrolujących środki finansowe. Użytkownik planuje budżet ustalając jego nazwę oraz kapitał początkowy. Budżet moze być powiększony, lub obciążony wydatkiem na wybrany cel. Po zarejestrowaniu zmian w kapitale aplikacja analizuje dokonane wydatki przedstawiając ich proporcje na wykresie kołowym',
            progress: { proc: 80, comm: 'Zawieszony' },
            link: 'https://github.com/radiol00/JudiCoinApp',
            photos: [coin1, coin2, coin3, coin4]
        },
        {
            name: 'My Gwent',
            technologies: ['Flutter'],
            description: 'Projekt prostej gry karcianej, którą tworzę głównie by rozszerzyć swoją wiedzę i doświadczenie z zakresu BLoC, animacji, fetchowania API, oraz sprawnego przemieszczania się po drzewie widgetów.\n\nWykorzystywane API: https://deckofcardsapi.com',
            progress: { proc: 20, comm: 'Realizowany' },
            link: 'https://github.com/radiol00/MyGwent',
            photos: [gwent1, gwent2]
        },
        {
            name: 'Icosehadron',
            technologies: ['C/C++', 'QtCreator'],
            description: 'Projekt zaliczeniowy z laboratorium grafiki i multimediów. Bryła generowana jest poprzez dzielenie ścian dwudziestościanu na mniejsze oraz rzutowanie jej na przestrzeń 3D. Następnie jest teksturowana wykorzystując współrzędne barycentryczne tekstury i każdej ściany. Na końcu wyliczane jest odpowiednie rzucanie cienia przez bryłę wykorzystując algorytm Ray Tracing.',
            progress: { proc: 100, comm: 'Ukończony' },
            link: 'https://github.com/radiol00/IcosehadronRender',
            photos: [ico1, ico2, ico3, ico4]
        }
    ]

    let buildProgressBar = () => {
        let i = projects[pickedProject].progress.proc;
        let bar = `linear-gradient(to right, #eba194 0% ${i}%, transparent ${i}% 100%)`;
        let comment = projects[pickedProject].progress.comm
        return (
            <div className="progress-bar-container">
                <div className="progress-bar" style={{ background: bar }}>
                    <span>{i}%</span>
                    <span>{comment}</span>
                </div>
            </div>
        )
    }

    useEffect(() => {
        let interval = setInterval(() => {
            if (!pauseInterval.current && !projectChosenByUser.current) { ticks.current++; }
            else { ticks.current = 0 }
            if (ticks.current) {
                if (ticks.current % 8 === 0) {
                    setSlideDirection('toleft');
                    if (pickedProject + 1 >= projects.length) {
                        setPickedProject(0);
                    } else {
                        setPickedProject(pickedProject + 1);
                    }
                    setCurrentPhoto(0);
                } else if (ticks.current % 2 === 0) {
                    if (currentPhoto + 1 >= projects[pickedProject].photos.length) {
                        setCurrentPhoto(0);
                    } else {
                        setCurrentPhoto(currentPhoto + 1);
                    }
                }
            }
        }, 1000);

        return (() => {
            window.clearInterval(interval)
        })
    }, [projects, pickedProject, projectChosenByUser, currentPhoto, ticks])

    return (
        <>
            <div className="pslider-title-container">
                <div className="left-slope-flipped"></div>
                <div className="pslider-title-content"><h4>Moje projekty</h4></div>
                <div className="right-slope-flipped"></div>
            </div>

            <div {...swipeHandlers} className="pslider-container">
                <SwitchTransition mode="out-in">
                    <CSSTransition nodeRef={transitionNode} key={pickedProject} classNames={{
                        enter: slideDirection === 'toleft' ? 'slide-enter-toleft' : 'slide-enter-toright',
                        enterActive: 'slide-enter-active',
                        exit: 'slide-exit',
                        exitActive: slideDirection === 'toleft' ? 'slide-exit-active-toleft' : 'slide-exit-active-toright',
                    }}
                        onEnter={() => {
                            setCurrentPhoto(0);
                        }}
                        addEndListener={(done) => {
                            transitionNode.current.addEventListener('transitionend', done, false);
                        }}

                    >
                        <div ref={transitionNode} className="pslider-content">
                            <div className="pslider-title-bar">
                                <div className="pslider-project-link">
                                    {projects[pickedProject].link !== '' ? <a href={projects[pickedProject].link} target="_blank" rel="noopener noreferrer"><img src={ghprojectlogo} alt="github project icon" /></a> : null}
                                </div>
                                <div className="pslider-project-title">
                                    {projects[pickedProject].name}
                                </div>
                                <div className="pslider-project-counterweight"></div>
                            </div>

                            <div className="pslider-image-slider">
                                <div className="pslider-project-technologies">
                                    <div className="pslider-project-technologies-content">
                                        {projects[pickedProject].technologies.map((technology) => {
                                            return (
                                                <div key={technology} className="pslider-project-technology">{technology}</div>
                                            )
                                        })}
                                    </div>
                                </div>
                                <div className="pslider-project-image" onClick={() => {
                                    pauseInterval.current = true;
                                    setIsLightboxOpened(true);
                                }}>
                                    {isLightboxOpened ?
                                        <Lightbox
                                            imagePadding={60.0}
                                            enableZoom={isMobile}
                                            mainSrc={projects[pickedProject].photos[photoIndex]}
                                            nextSrc={projects[pickedProject].photos[(photoIndex + 1) % projects[pickedProject].photos.length]}
                                            prevSrc={projects[pickedProject].photos[(photoIndex + projects[pickedProject].photos.length - 1) % projects[pickedProject].photos.length]}
                                            onMovePrevRequest={() => {
                                                setPhotoIndex((photoIndex + projects[pickedProject].photos.length - 1) % projects[pickedProject].photos.length)
                                            }}
                                            onMoveNextRequest={() => {
                                                setPhotoIndex((photoIndex + 1) % projects[pickedProject].photos.length)
                                            }
                                            }
                                            onCloseRequest={() => { setIsLightboxOpened(false); setPhotoIndex(0); pauseInterval.current = false; }} />
                                        : null}
                                    <img src={projects[pickedProject].photos[currentPhoto]} alt="Project" />
                                    <i className="material-icons">search</i>
                                </div>

                                <div className="pslider-project-progress">
                                    {buildProgressBar()}
                                </div>
                            </div>

                            <div className="pslider-project">
                                <div className="pslider-project-description">
                                    <textarea readOnly={true} defaultValue={projects[pickedProject].description} />
                                </div>
                            </div>
                        </div>
                    </CSSTransition>
                </SwitchTransition>
            </div>

            <div className="pslider-stripe-container">
                {projects.map((value, i) => {
                    return <div key={i} onClick={() => {
                        setSlideDirection('toleft');
                        projectChosenByUser.current = true;
                        setTimeout(() => {
                            projectChosenByUser.current = false;
                        }, 10000);
                        setPickedProject(i);
                    }} className={i === pickedProject ? "active" : ""}></div>
                })}
            </div>

        </>
    )
}