import React from 'react';
import './heropage.css';
import photo from './../../assets/myphoto.png';
import linkedinlogo from '../../assets/lilogoorange.png';
import githublgoo from '../../assets/ghlogoorange.png';

export default function HeroPage() {

    return (
        <div className="heropage-container">
            <div className="heropage-content">
                <div className="heropage-leftside">
                    <div className="heropage-firstline"><div className="heropage-hello"><span>Cześć!</span></div> Jestem</div>
                    <h1 className="heropage-name">Tobiasz Pokorniecki</h1>
                    <p className="heropage-description">
                        student informatyki inżynierskiej <br />
                        front-end developer <br />
                        mobile app developer
                    </p>
                    <div className="heropage-socials">
                        <a href="https://www.linkedin.com/in/tobiasz-pokorniecki-b211371b2/" target="_blank" rel="noopener noreferrer"><img src={linkedinlogo} alt="linkedin" /></a>
                        <a href="https://github.com/radiol00" target="_blank" rel="noopener noreferrer"><img src={githublgoo} alt="github" /></a>
                    </div>
                </div>
                <div className="heropage-rightside">
                    <div className="heropage-image">
                        <img src={photo} alt="Tobiasz Pokorniecki in a frame" />
                    </div>
                </div>
            </div>
        </div>
    )
}