import React from 'react';
import './aboutme.css';


export default function AboutMe() {


    return (
        <div className="aboutme-container">
            <div className="aboutme-content">
                <div className="aboutme-title">
                    <h4>O mnie</h4>
                </div>
                <div className="aboutme-text">
                    <p>Jestem studentem III roku Informatyki inżynierskiej na Uniwerystecie Mikołaja Kopernika w Toruniu.
                        Przez technikum i&nbsp;studia ciągnęły się za mną najróżniejsze języki programowania,
                        jednak najbardziej zainteresowała mnie sfera WebApp i&nbsp;MobileApp developmentu.
                        Swoją przygodę z aplikacjami zacząłem w 2019 roku tworząc, w oparciu o React.js, projekt zespołowy JudiBell,
                        który zajął drugie miejsce w rankingu pod względem potencjału biznesowego. Obecnie rozwijam swoje umiejętności we Flutter oraz framework'u Django.</p>
                </div>
            </div>
        </div>
    )
}